.modal-movimiento section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5px;
}

.modal-movimiento .button-container {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  margin-top: 10px;
}
.modal-movimiento .button-container button {
  border-radius: 20px;
  border: 1px solid #079434;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 110px;
}

.modal-movimiento .button-container button:hover {
  background-color: #079434;
  border: none;
  color: white;
}
